body {
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5; /* Light Gray */
  color: #333; /* Dark Gray */
  margin: 0;
  padding: 0;
}

.otaku-app-container,
.otaku-mangapage-container {
  font-family: 'Arial', sans-serif;
  background-color: #fff; /* White */
  color: #333; /* Dark Gray */
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  margin-top: 100px;
}


/* Title Style */
.otaku-title {
  font-size: 28px;
  font-weight: bold;
  color: #333; /* Dark Gray */
}

/* Search Container */
.otaku-search-container {
  text-align: center;
  margin: 20px;
}

/* Input and Select Styles */
.otaku-input,
.otaku-select {
  padding: 14px;
  font-size: 18px;
  border: 2px solid #3498db; /* Dodger Blue */
  background-color: #3498db; /* Dodger Blue */
  color: #ecf0f1; /* Clouds */
  border-radius: 8px;
  margin-right: 10px;
  margin-bottom: 20px;
}

/* Buttons Styles */
.otaku-button,
.otaku-fetch-button {
  padding: 14px 28px;
  font-size: 18px;
  background-color: #3498db; /* Dodger Blue */
  border: 2px solid #3498db; /* Dodger Blue */
  color: #ecf0f1; /* Clouds */
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: background-color 0.3s ease;
}

.otaku-button:hover,
.otaku-fetch-button:hover {
  background-color: #2980b9; /* Belize Hole */
}

/* Results and Manga Page Containers */
.otaku-results,
.otaku-manga-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Manga Item and Page Button Styles */
.otaku-manga-item,
.otaku-manga-page-button {
  margin: 10px;
  text-align: center;
  font-size: 16px;
}

/* Manga Image and Page Image Styles */
.otaku-manga-image,
.otaku-manga-page-img {
  width: 100%;
  max-width: 600px;
  height: auto;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

/* Selected Chapter Style */
.otaku-selected-chapter {
  margin-top: 10px;
}

/* Dropdown Styles */
.otaku-dropdown {
  width: 300px;
}

.otaku-dropdown-option {
  font-size: 16px;
  color: #333;
}

.otaku-dropdown-option:hover {
  background-color: #e74c3c; /* Alizarin */
  color: #ecf0f1;
}

/* Dropdown No Results and Placeholder Styles */
.otaku-dropdown-no-results,
.otaku-dropdown-placeholder {
  color: #e74c3c; /* Alizarin */
}

/* Search Input Style */
.otaku-search-input {
  width: 300px;
  margin-top: 10px;
}

/* Dark Mode Styles */
.otaku-dark-mode {
  background-color: #fff; /* White */
  color: #333; /* Dark Gray */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.search-title {
  font-family: 'OtakuFont';
  margin-bottom: 20px;
  font-size: 24px;
}

button {
  padding: 14px 28px;
  font-size: 18px;
  background-color: #3498db; /* Dodger Blue */
  border: 2px solid #3498db; /* Dodger Blue */
  color: #ecf0f1; /* Clouds */
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 20px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2980b9; /* Belize Hole */
}

.otaku-results {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.otaku-info {
  position: relative;
  margin-right: 20px;
  margin-bottom: 20px;
  top: 120px;
  border: 2px solid #3498db; /* Dodger Blue */
  border-radius: 10px;
}

.otaku-info img {
  height: 300px;
  width: auto;
  border-radius: 10px;
  cursor: pointer;
}

.otaku-info p {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 95%;
  background: rgba(0, 0, 0, 0.5);
  color: #ecf0f1;
  text-align: center;
  padding: 5px;
  margin: 0;
  font-size: 14px;
  transition: color 0.3s ease;
}

.otaku-info p:hover {
  color: #e74c3c; /* Alizarin */
}

.otaku-chapter-container {
  font-family: 'Arial', sans-serif;
  background-color: #fff; /* White */
  padding: 20px;
  border-radius: 10px;
  margin: 20px auto;
  max-width: 80%;
  margin-top: 100px;
}

.otaku-heading {
  color: #3498db; /* Dodger Blue */
}

.otaku-content-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}

.otaku-manga-image-container {
  flex: 1;
}

.otaku-manga-page-img {
  max-width: 100%;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.otaku-manga-details-container {
  flex: 2;
  margin-left: 20px;
}

.otaku-select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #3498db; /* Dodger Blue */
  border-radius: 5px;
  background-color: #fff;
  color: #333;
  outline: none;
}

.otaku-option {
  color: #333;
}

.otaku-manga-details {
  margin-top: 20px;
}

.otaku-manga-details h3 {
  color: #3498db; /* Dodger Blue */
  margin-bottom: 10px;
}

.otaku-manga-details p {
  color: #555;
}

/* Updated styles for genres and status */
.otaku-genres,
.otaku-status {
  display: inline-block;
  padding: 5px 10px;
  margin: 5px 0;
  border: 1px solid #3498db; /* Dodger Blue */
  border-radius: 5px;
  color: #3498db; /* Dodger Blue */
}

.otaku-selected-chapter {
  margin-top: 10px;
}

.navbar {
  background-color: #3498db;
  height: 100vh;
  width: 80px;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  margin-top: 80px;
  z-index: 2;
}

.nav-link {
  text-decoration: none;
  color: #ecf0f1;
  font-size: 18px;
  margin: 15px 0;
  width: 100%;
  text-align: center;
  transition: background-color 0.3s ease;
}

.nav-link:hover {
  background-color: #2980b9;
}

.page-content {
  margin-left: 80px;
  padding: 20px;
}

/* Search Navigation Bar */
.search-navbar {
  background-color: #fff;
  height: 60px;
  position: fixed;
  display: flex;
  align-items: center;
  padding: 10px 10px;
  gap: 100px;
  z-index: 1;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  top: 0px;
}


.search-title {
  color: #3498db;
  font-size: 24px;
}

.search-button {
  position: relative;
  margin: 0;
  padding: 10px;
  font-size: 12px;
  border: none;
  top: 0px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  right: 160px;
  z-index: 2;
}

.search-button:hover {
  background-color: #2980b9;
}

.burger-menu-icon {
  font-size: 24px;
  cursor: pointer;
  display: none; /* Hide by default */
}

/* Responsive Styles */
@media only screen and (max-width: 800px) {
  .otaku-app-container,
  .otaku-mangapage-container {
    padding: 10px; /* Adjust padding for smaller screens */
  }

  .otaku-title {
    font-size: 20px; /* Decrease font size for smaller screens */
  }

  .otaku-search-container {
    margin: 10px;
  }

  .otaku-input,
  .otaku-select,
  .otaku-button,
  .otaku-fetch-button {
    padding: 7px 14px;
    font-size: 12px;
    background-color: #3498db; /* Dodger Blue */
    border: 2px solid #3498db; /* Dodger Blue */
    color: #ecf0f1; /* Clouds */
    border-radius: 8px;
    cursor: pointer;
    margin-bottom: 20px;
    transition: background-color 0.3s ease;
  }
  
  .otaku-fetch-button-container{
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 10px;
  z-index: 1;
  width: 100%;
  top: 0px;
  }
  .otaku-manga-item,
  .otaku-manga-page-button {
    margin: 5px;
    font-size: 14px;
  }

  .otaku-manga-image,
  .otaku-manga-page-img {
    max-width: 100%;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }

  .otaku-selected-chapter {
    margin-top: 5px;
  }

  .otaku-dropdown {
    width: 100%;
  }

  .otaku-search-input {
    width: 100%;
    margin-top: 5px;
  }

  .search-container {
    margin-top: 10px;
  }

  .nav-link {
    text-decoration: none;
  color: #000; /* Black text color */
  font-size: 18px;
  margin: 5px 0;
  width: auto; /* Set width to auto to fit content */
  text-align: center;
  transition: background-color 0.3s ease;
  }

  .page-content {
    margin-left: 0;
  }
